import React from 'react'

import { Menu } from 'antd';

const { SubMenu } = Menu;


class ncovtable extends React.Component{
    constructor(props){
        super(props)
        this.state={}
    }
    render(){
        var countryes = JSON.parse(this.props.country)
        var province_city = JSON.parse(this.props.province_city)
        // console.log(this.props.province_city)
        return(
            <div id="ncov-table"  className="no-selected">
                <div className="ncov-title">中国(含港澳台)病例</div>
                    <div className="ncov-box">
                        <div className="ncov-row-tab">
                            <span>省市</span>
                            <span>确诊</span>
                            <span>治愈</span>
                            <span>死亡</span>
                        </div>
                    </div>
                    <div className="ncov-box">
                        {
                            province_city.map((v,k)=>{
                                if(v.cities.length > 0){
                                    return (
                                        <Menu
                                            key = {v.provinceShortName}
                                            style={{ width: '100%',listStyle:'none' }}
                                            // defaultSelectedKeys={['1']}
                                            defaultOpenKeys={['湖北']}
                                            mode={'inline'}
                                            theme={'light'}
                                            selectable={false}
                                        >
                                            <SubMenu
                                                key={v.provinceShortName}
                                                title={
                                                <span>
                                                    <div className="ncov-row count">
                                                        <span >{v.provinceShortName}</span>
                                                        <span className="quezhen">{v.confirmedCount>0?v.confirmedCount:""}</span>
                                                        <span className="zhiyu">{v.curedCount>0?v.curedCount:""}</span>
                                                        <span className="siwang">{v.deadCount>0?v.deadCount:""}</span>
                                                    </div>
                                                </span>
                                                }
                                            >
                                                {
                                                    v.cities.map((v1,k1)=>{
                                                        return (
                                                            <Menu.Item key={v1.cityName} style={{listStyle:"none"}}>
                                                                <div className="ncov-row">
                                                                    <span className={v1.cityName.length >4 ?"neimeng":"other" }>{v1.cityName}</span>
                                                                    <span className="quezhen">{v1.confirmedCount>0?v1.confirmedCount:""}</span>
                                                                    <span className="zhiyu">{v1.curedCount>0?v1.curedCount:""}</span>
                                                                    <span className="siwang">{v1.deadCount>0?v1.deadCount:""}</span>
                                                                </div>
                                                            </Menu.Item>
                                                        )
                                                    })
                                                }
                                            </SubMenu>
                                        </Menu>
                                    )
                                }else{
                                    return (
                                        <Menu
                                            key = {v.provinceShortName}
                                            style={{ width: '100%',listStyle:'none' }}
                                            // defaultSelectedKeys={['1']}
                                            // defaultOpenKeys={['sub1']}
                                            mode={'inline'}
                                            theme={'light'}
                                            selectable={false}
                                        >
                                            <Menu.Item key={v.provinceShortName}>
                                                <span>
                                                    <div className="ncov-row count">
                                                        <span>{v.provinceShortName}</span>
                                                        <span className="quezhen">{v.confirmedCount>0?v.confirmedCount:""}</span>
                                                        <span className="zhiyu">{v.curedCount>0?v.curedCount:""}</span>
                                                        <span className="siwang">{v.deadCount>0?v.deadCount:""}</span>
                                                    </div>
                                                </span>
                                            </Menu.Item>
                                        </Menu>
                                    )
                                }
                            })
                        }
                    </div>
                    <div className="ncov-title">海外病例</div>
                    <div className="ncov-box">
                        <div className="ncov-row-tab">
                            <span>国家</span>
                            <span>确诊</span>
                            <span>治愈</span>
                            <span>死亡</span>
                        </div>
                    </div>
                    <div className="ncov-box">
                        {
                            countryes.map((v,k)=>{
                                return(
                                    <Menu key={k}
                                        style={{ width: '100%',listStyle:'none' }}
                                        // defaultSelectedKeys={['1']}
                                        // defaultOpenKeys={['sub1']}
                                        mode={'inline'}
                                        theme={'light'}
                                        selectable={false}
                                    >
                                        <Menu.Item key={v.name}>
                                            <span>
                                                <div className="ncov-row count">
                                                    <span>{v.name}</span>
                                                    <span className="quezhen">{v.confirmedCount>0?v.confirmedCount:""}</span>
                                                    <span className="zhiyu">{v.curedCount>0?v.curedCount:""}</span>
                                                    <span className="siwang">{v.deadCount>0?v.deadCount:""}</span>
                                                </div>
                                            </span>
                                        </Menu.Item>
                                    </Menu>
                                )
                            })
                        }
                    </div>
                    </div>
        )
    }

}

export default ncovtable

