import React from 'react';
import Header from '../component/header';
import Footer from  '../component/footer';
import CoinListComponent from  '../component/coinlist';

export default class Cate extends React.Component {
  constructor(props){
    super(props)
    this.state={
      type:0,
      login_info:{"is_login":0,"token":""},
    }
  }
  componentWillMount(){
    this.setState({type:this.props.match.params.type,login_info:global.is_login()})
  }
  render(){
      // console.log("cate page render")
      return (
        <div id="container">
          <Header />
          <CoinListComponent type={this.state.type} is_login={this.state.login_info.is_login} token={this.state.login_info.token} />
          <Footer />
        </div>
      )
  }
}

