import React from 'react'
import ReactEcharts from "echarts-for-react";
import 'echarts/map/js/china';


class ncovchinamap extends React.Component{
    constructor(props){
        super(props)
        this.state={}
    }

    getChinaMapOption(){
        // console.log(this.props.province)
        return {
            title: {
                text: '  全国疫情地图',
                subtext: '   单击可查看各省市病例数',
                sublink: 'http://www.nhc.gov.cn/',
                left: 'left'
            },
            tooltip: {
                trigger: 'item',
                showDelay: 0,
                transitionDuration: 0.2,
                formatter: function (params) {
                    if (params.data === undefined) {
                        return params.name + "0"
                    }else{
                        return params.name +"确诊" +params.data.confirmedCount+"例"
                    }
                    
                }
            },
            visualMap: {
                show:false,
                left: 'right',
                min: 0,
                max: this.props.map_max_count,
                inRange: {
                    color: ['#ffffff', '#f46d43', '#d73027', '#a50026',"#600000"]
                },
                text: ['>1000', '0'],           // 文本，默认为数值文本
                calculable: false
            },
            // toolbox: {
            //     show: false,
            //     //orient: 'vertical',
            //     left: 'right',
            //     top: 'top',
            //     feature: {
            //         dataView: {readOnly: false},
            //         restore: {},
            //         saveAsImage: {}
            //     }
            // },
            series: [
                {
                    name: '中国',
                    type: 'map',
                    mapType: 'china',
                    // selectedMode: 'multiple',
                    label: {
                        show: false
                    },
                    itemStyle: {
                        normal: {//未选中状态
                            // borderWidth:1,//边框大小
                            // borderColor:'lightgreen',
                            areaColor: '#fff',//背景颜色
                            label: {
                                show: true,
                                color:"#7b7b7b",
                                fontSize:"7"
                            }
                        },
                        emphasis: {// 也是选中样式
                            // borderWidth:1,
                            // borderColor:'#fff',
                            areaColor: '#e6f7ff',
                            label: {
                                show: true,
                                textStyle: {
                                    // color: '#fff'
                                }
                            }
                        }
                    },
                    data: JSON.parse(this.props.province)
                }
            ]
        };
    }

    
    render(){
        return (<div id="ncov-map ncov-box">
            {/* <div className="ncov-title">疫情地图</div> */}
            <ReactEcharts
                option={this.getChinaMapOption()}
                notMerge={true}
                lazyUpdate={true}
                style={{minHeight:"400px", width: '100%'}}
                className='echarts-for-echarts'
                theme={"theme_name"}
                />
        </div>)
    }
}

export default ncovchinamap