import React from 'react'
import Request from '../utils/request'
import VersionShareInfo from './version_share_info'

class coinlist extends React.Component {
    constructor(props){
        super(props)
        this.state={
            list:[],
            vlist:[],
            name:"金币记录"
        }
        this.getList = this.getList.bind(this)
    }
    getList(){
        Request({
            url:'/user/coinlist',
            method:'post',
            headers:{"X-Token":this.props.token},
            data:{"type":parseInt(this.props.type)}
        }).then((res)=>{
            if (res.code === 200) {
                console.log(res.data)
                this.setState({list:res.data.list,vlist:res.data.vlist})
            }
        })
    }
    componentWillMount(){
        this.getList()
    }
    componentDidMount(){
        var name = "金币记录"
        switch (parseInt(this.props.type)){
            case 1: 
                name ="充值记录"
                break;
            case 2:
                name = "兑换记录"
                break;
            case 3: 
                name = "系统奖励"
                break;
            case 4:
                name = "我的收入"
                break;
            default:
                break;

        }
        global.setTitle(name+" - "+global.constants.base_title)
        this.setState({name:name})
    }
    render () {
        return (
            <div id="exchange-page" className="main-content page-page" style={{paddingTop:"50px"}}>
                    <div className="post-content">
                        <h3>{this.state.name}</h3>
                    </div>
                    {
                        this.state.list.map((v)=>{
                            var desc = ""
                            switch (v.type){
                                case 1: 
                                    desc ="通过订单充值，获得金币+"+v.coin
                                    break;
                                case 2:
                                    desc = "兑换了《"+v.name+"》，金币"+v.coin
                                    break;
                                case 3: 
                                    desc = "系统奖励了金币+"+v.coin
                                    break;
                                case 4:
                                    desc = "用户购买了你分享的《"+v.name+"》，金币+"+v.coin
                                    break;
                                default:
                                    break;
                            }
                            return (
                                <div key={v.id}>
                                    <div className="comment-form">
                                        <div className="comment-meta"> 
                                        <span style={{color:"gray"}}>{v.order_id}</span>
                                        </div>
                                        <div className="comment-content"> 
                                            <span className="comment-author-at"></span>
                                            <p>
                                            {desc}
                                            </p>
                                            <p></p>
                                        </div>
                                        {
                                            // v.type === 2 ? <VersionShareInfo type={v.type} share_link="" share_secret=""  share_content="" /> :""

                                            this.state.vlist.map((vv)=>{
                                                if (vv.vid === v.oid) {
                                                    return (<VersionShareInfo type={v.type} share_link={vv.share_link} share_secret={vv.share_secret}  share_content={vv.share_content} />)
                                                } else{
                                                    return ""
                                                }
                                            })
                                        }
                                        <div className="comment-meta" style={{textAlign:"right"}}> 
                                            <time className="comment-time">{global.getLocalTime(v.created_time)}</time>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }


                    {/* <div id="comment-2806">
                        <div className="comment-form">
                            <div className="comment-meta"> 
                            <span style={{color:"gray"}}>l1231208993849278492789</span>
                            </div>
                            <div className="comment-content"> 
                                <span className="comment-author-at"></span>
                                <p>
                                这里是描述
                                </p>
                                <p></p>
                            </div>
                            <div className="comment-meta" style={{textAlign:"right"}}> 
                                <time className="comment-time">2019-02-11 23:22:11</time>
                            </div>
                        </div>
                    </div> */}
                </div>
        )
    }
}

export default coinlist