import React from 'react'
import { Link } from 'react-router-dom';
import Request from '../utils/request'
import Gallery from './gallery'

export default class Item extends React.Component {
    constructor(props){
        super(props)
        this.state={
            data:{"cate_info":"[]"},
            resource_list:[],
            oid:this.props.oid,
            is_loading:false,
            show_desc:"none",
            show_btn:"none"
        }
        this.getItemInfo = this.getItemInfo.bind(this)
        this.getResourceList = this.getResourceList.bind(this)
        this.showDesc = this.showDesc.bind(this)
    }
    getItemInfo(){
        Request({
            url:'/info',
            method:'post',
            header:{},
            data:{"oid":this.state.oid}
          }).then((res)=>{
              if (res.code === 200) {
                  var show_btn = "none"
                if (res.data.desc.length > 200){
                    show_btn = ""
                }
                  this.setState({data:res.data,is_loading:true,show_btn:show_btn})
              }
          })
    }

    showDesc(){
        this.setState({show_desc:"",show_btn:"none"})
    }

    getResourceList(){
        Request({
            url:'/itemlist',
            method:'post',
            header:{},
            data:{"oid":this.state.oid}
          }).then((res)=>{
              if (res.code === 200) {
                
                  this.setState({resource_list:res.data})
              }
          })
    }

    componentWillMount(){
        this.getItemInfo()
        this.getResourceList()
    }
    render(){
        if (!this.state.is_loading) {
            return(
                <div className="bg-white">
                        <div style={{color:"gray",marginTop:"50px",textAlign:"center",padding:"20px"}}>正在加载数据...</div>
                </div>
            )
        }
        var show_name = this.state.data.name
        if (this.state.data.play_year!==0){
            show_name += "（"+this.state.data.play_year+"）"
        }
        //
        var desc = this.state.data.desc
        var otherdesc = ""
        if (this.state.data.desc.length > 200){
            desc=this.state.data.desc.substring(0,200)
            otherdesc = this.state.data.desc.substring(200)
        }
        global.setTitle(this.state.data.name+" | "+global.constants.base_title)
        return (
            
            <div className="bg-white" id="item-page">
                <article className="main-content page-page" style={{paddingBottom:'20px'}} itemScope="" itemType="http://schema.org/Article">
                    <div style={{position:"absolute",right:"20px",fontSize: "40px",top: "50px",color:"#D8552F"}}>{this.state.data.douban_score===0?"":this.state.data.douban_score/10}</div>
                    
                    <div className="post-header">
                        <h1 className="post-title" itemProp="name headline">{show_name}</h1>
                        <div className="post-data"></div>
                    </div>
                    
                    <div className="post-content" itemProp="articleBody">
                        <p className="post-tags">
                            {
                                JSON.parse(this.state.data.relation_style).map((tag,k)=>{
                                    if (k<3) { 
                                        return (<Link key={tag.short_str} to={tag.short_str}>{tag.name}</Link>)
                                    }else {
                                        return ""
                                    }
                                })
                            }
                        </p>
                    </div>
                    {/* <div style={{"margin":"15px 0 0 0"}}></div> */}
                    
                    <p><strong>剧情简介</strong></p>

                    <div className="post-content">
                        {/* <p>导演: {this.state.data.director}</p>
                        <p>编剧: {this.state.data.screen_write}</p>
                        <p>主演: {this.state.data.actors}</p> */}
                        <p>
                            {desc}
                            <span style={{color:"gray",cursor:"pointer",display:this.state.show_btn}} onClick={this.showDesc}>查看更多</span>
                            <span style={{display:this.state.show_desc}}>{otherdesc}</span>
                        </p>

                        {/* <figure className="wp-block-image">
                            <img src={this.state.data.images} alt="Hello World" className="wp-image-23" srcSet={this.state.data.images} sizes={{maxWidth:'640px'}}></img>
                        </figure> */}
                    </div>
                    {/* <p><strong>剧照</strong></p> */}
                    <Gallery images={JSON.parse(this.state.data.images)} showFullscreenButton={false} />
                    <p><strong>主演</strong></p>
                    {JSON.parse(this.state.data.actors).map((item,k)=>{
                        if (k <=5 ){
                            return (<span key={item.short_str} ><Link to={item.short_str}> {item.name} </Link></span>)
                        }else{
                            return ""
                        }
                        
                    })}
                    等
                    
                    {/* <p><strong>更多信息</strong></p>
                    <p><a href="https://mingjunli.gitbooks.io/mat/content/" target="_blank" rel="noopener noreferrer" >https://mingjunli.gitbooks.io/mat/content/</a></p> */}
                    
                </article>
                
                <div id="respond-post-334" className="comment-container">
                    <div id="comments" className="clearfix">
                        <p style={{}}><strong>资源列表</strong></p>
                    </div>

                    <div id="comments" className="clearfix" style={{padding:"0"}}>
                        {
                            this.state.resource_list.map(v=>{
                                var avatar = "/avatar/default.png"
                                if (v.user.avatar !== "") {
                                    avatar = v.user.avatar
                                }
                                return (
                                    <ol className="comment-list" key={v.item.vid}>
                                        <li id="li-comment-2806" className="comment-body comment-parent comment-odd">
                                            <div id="comment-2806">
                                                <div className="comment-view">
                                                    <div className="comment-header">
                                                        <img className="avatar" src={global.getStaticHost()+avatar} width="80" height="80" alt="用户头像" ></img>
                                                        <span className="comment-author">{v.user.nickname}</span>
                                                        
                                                    </div>
                                                    <div className="comment-content"> 
                                                    <span className="comment-author-at"></span>
                                                        <p>
                                                            分享了{v.item.oname}
                                                        </p>
                                                        <span className="comment-author-at"></span>
                                                        <p>
                                                            {v.item.desc}
                                                        </p>
                                                        <p></p>
                                                    </div>
                                                    
                                                    <div className="comment-meta"> 
                                                        <time className="comment-time">{global.getLocalTime(v.item.created_time)}</time>
                                                        <span className="comment-reply" style={{float:"right",display:"block"}}><Link to={"/user/exchange/"+v.item.vid}>兑换({v.item.sold_coin}金币)</Link></span>
                                                        {/* <span className="comment-reply" style={{float:"right",display:"block"}}><Link to="/user/edit">购买(¥{v.item.sold_coin/10}.00)</Link></span> */}
                                                    </div>

                                                    <div className="comment-view" style={{padding:"20px 0"}}>
                                                        <Gallery images={JSON.parse(v.item.detail_images)} />
                                                    </div>
                                                    
                                                </div>
                                            </div>
                                        </li>
                                    </ol>
                                )
                            })
                        }
                        
                        {/* <p></p> */}
                        
                    </div>
                </div>
                <div id="respond-post-334" className="comment-container">
                    <div id="comments" className="clearfix">
                        <p style={{}}><strong>精选评论</strong></p>
                    </div>
                   
                    <div id="comments" className="clearfix" style={{padding:"0"}}>
                        <ol className="comment-list">
                        {
                            JSON.parse(this.state.data.comments).map((comment,k)=>{
                                return (
                                    <li id="" key={k} className="comment-body comment-parent comment-odd">
                                        <div id="comment-2806" style={{color:"gray",fontSize:"1em"}}>
                                            <div className="comment-view">
                                                <p>
                                                {comment}
                                                </p>
                                            </div>
                                        </div>
                                    </li>
                                )
                            })
                        }
                            
                        </ol>
                    </div>
                </div>
                <div className="comment-container" style={{color:"#gray",textAlign:"center",padding:"20px"}}></div>
            </div>
        )
    }
}