import React from 'react'
import ReactEcharts from "echarts-for-react";
import 'echarts/map/js/province/hubei';


class ncovhubeimap extends React.Component{
    constructor(props){
        super(props)
        this.state={}
    }

    getChinaMapOption2(){
        var province_city = JSON.parse(this.props.province_city)
        var mapData = []
        if(province_city.length>0){
            
            province_city[0]['cities'].map((v,k)=>{
                if (v.cityName === "恩施" || v.cityName === "恩施州" ){
                    v.name = "恩施土家族苗族自治州"
                }else if (v.cityName === "神农架林区") {
                    v.name = v.cityName
                } else{
                    v.name = v.cityName+"市"
                }
                v.value = v.confirmedCount
                return mapData.push(v)
            })

            // console.log(mapData)
        }
        return {
            title: {
                text: '  湖北疫情地图',
                subtext: '   单击可查看各地市病例数',
                sublink: 'http://wjw.hubei.gov.cn/',
                left: 'left'
            },
            tooltip: {
                trigger: 'item',
                showDelay: 0,
                transitionDuration: 0.2,
                formatter: function (params) {
                    if (params.data === undefined) {
                        return params.name + "0"
                    }else{
                        return params.name +"确诊" +params.data.confirmedCount+"例"
                    }
                    
                }
            },
            visualMap: {
                show:false,
                left: 'right',
                min: 0,
                max: this.props.map_max_count,
                inRange: {
                    color: ['#ffffff', '#f46d43', '#d73027', '#a50026','#600000']
                },
                text: ['', ''],           // 文本，默认为数值文本
                calculable: false
            },
            // toolbox: {
            //     show: false,
            //     //orient: 'vertical',
            //     left: 'right',
            //     top: 'top',
            //     feature: {
            //         dataView: {readOnly: false},
            //         restore: {},
            //         saveAsImage: {}
            //     }
            // },
            series: [
                {
                    name: '湖北',
                    type: 'map',
                    mapType: '湖北',
                    // selectedMode: 'multiple',
                    label: {
                        show: false
                    },
                    itemStyle: {
                        normal: {//未选中状态
                            // borderWidth:1,//边框大小
                            // borderColor:'lightgreen',
                            areaColor: '#fff',//背景颜色
                            label: {
                                show: true,
                                color:"#7b7b7b",
                                fontSize:"7"
                            }
                        },
                        emphasis: {// 也是选中样式
                            // borderWidth:1,
                            // borderColor:'#fff',
                            areaColor: '#e6f7ff',
                            label: {
                                show: true,
                                textStyle: {
                                    // color: '#fff'
                                }
                            }
                        }
                    },
                    data:mapData
                }
            ]
        };
    }
    render(){
        return (<div id="ncov-map ncov-box">
            {/* <div className="ncov-title">疫情地图</div> */}
            <ReactEcharts
                option={this.getChinaMapOption2()}
                notMerge={true}
                lazyUpdate={true}
                style={{minHeight:"400px", width: '100%'}}
                className='echarts-for-echarts'
                theme={"theme_name"}
                />
        </div>)
    }
}

export default ncovhubeimap