import React from 'react'
import { Link } from 'react-router-dom';
import Request from '../utils/request'
import { Redirect } from 'react-router-dom'

export default class User extends React.Component {
    constructor(props){
        super(props)
        this.state={
            userinfo:{
                avatar: "",
                coin: 0,
                nickname: "",
                openid: "",
                profile: "",
                role: 0
            },
            edit_baseinfo:0,
            errTips:"",
            errCoinMsg:"",
            orderid:"",
            is_pull_order:0,
            is_login:0,
        }
        this.getUserInfo = this.getUserInfo.bind(this)
        this.editBaseInfo = this.editBaseInfo.bind(this)
        this.saveBaseInfo = this.saveBaseInfo.bind(this)
        this.changeNickName = this.changeNickName.bind(this)
        this.changeProfile = this.changeProfile.bind(this)
        this.addCoin = this.addCoin.bind(this)
        this.setAvatar = this.setAvatar.bind(this)
    }

    getUserInfo(){
        Request({
            url:'/user/info',
            method:'post',
            headers:{"X-Token":this.props.token},
            data:{"oid":this.state.oid}
          }).then((res)=>{
              if (res.code === 200) {
                var avatar = "/avatar/default.png"
                var nickname = "用户昵称"
                var profile  = "您还没有填写个人简介"
                if (res.data.avatar === "") {
                    res.data.avatar = avatar
                }
                if (res.data.nickname === "") {
                    res.data.nickname = nickname
                }
        
                if (res.data.profile === "") {
                    res.data.profile = profile
                }
                // console.log(res)
                this.setState({userinfo:res.data,is_login:1})
              }else {
                console.log(res.msg) 
                if (res.code === 10018) {
                    //need login
                    console.log(this.props.history.location.pathname)
                    window._.setStore("token","")
                    // this.props.history.push('/user/login')
                    
                }
                // this.setState({errTips:res.msg})
              }
          })
    }

    editBaseInfo(){
        this.setState({edit_baseinfo:1})
    }

    saveBaseInfo(){
        if (this.state.nickname === "" ){
            this.setState({errTips:"用户昵称不能设置为空"})
            return
        }

        this.setState({edit_baseinfo:0})

        Request({
            url:'/user/updatebaseinfo',
            method:'post',
            headers:{"X-Token":this.props.token},
            data:{"nickname":this.state.userinfo.nickname,"profile":this.state.userinfo.profile,avatar:this.state.userinfo.avatar}
          }).then((res)=>{
              if (res.code === 200) {
                this.setState({edit_baseinfo:0})
              }else {
                // console.log(res.msg) 
                this.setState({errTips:res.msg})
              }
          })
    }

    changeNickName(){
        var userinfo = this.state.userinfo
            userinfo.nickname = this.nickname.value
        this.setState({userinfo:userinfo})
    }
    changeProfile(){
        var userinfo = this.state.userinfo
            userinfo.profile = this.profile.value
        this.setState({userinfo:userinfo})
    }
    setAvatar(v,e){
        console.log(v,e)
        var userinfo = this.state.userinfo
            userinfo.avatar = v
        this.setState({userinfo:userinfo})
    }

    addCoin(){
        
        var order_id = this.order_id.value.trim()
        if (order_id === "" ){
            this.setState({errCoinMsg:"订单号不能为空"})
            return
        }

        if (!order_id.match(/^([0-9]{18,20})$/i)) {
            this.setState({errCoinMsg:"订单号格式错误"})
            return
        }

        if (this.state.is_pull_order === 1) {
            return
        }
        
        this.setState({errCoinMsg:"正在同步订单...",orderid:order_id,is_pull_order:1})
        Request({
            url:'/user/pullorder',
            method:'post',
            headers:{"X-Token":this.props.token},
            data:{"order_id":order_id}
          }).then((res)=>{
              console.log(res)
              if (res.code === 200) {
                if (res.data.add === 1) {
                    var userinfo = this.state.userinfo
                    userinfo.coin += res.data.coin
                    this.setState({userinfo:userinfo})
                }
                this.setState({errCoinMsg:"订单同步成功，金币+"+res.data.coin,is_pull_order:0})
              }else {
                // console.log(res.msg) 
                this.setState({errCoinMsg:res.msg,is_pull_order:0})
              }
          })
    }

    componentWillMount(){
        global.setTitle("用户中心 - "+global.constants.base_title)
        this.setState({is_login:this.props.is_login})
        this.getUserInfo()
    }

    componentWillUnmount() {
        
    }

    componentDidMount(){
        
    }


    render(){
        // console.log(this.state.is_login)
        if (this.state.is_login === 0) {
            return (<Redirect to="/user/login" />);
        } else {
            return (
                <div id="login-page" className="main-content page-page" style={{paddingTop:"50px"}}>
                    <div className="post-content">
                        <h3>基本信息</h3>
                    </div>
                    <div className="clearfix" style={{}}>
                        <ol className="comment-list">
                            <li id="" className="comment-body comment-parent comment-odd" style={{border:"none",borderBottom:"1px solid #ccc"}}>
                                <div id="comment-2806">
                                    <div className="comment-view">
                                        <div className="comment-header">
                                            <img className="avatar" src={global.getStaticHost()+this.state.userinfo.avatar} width="80" height="80" alt="头像" ></img>
                                            <span className="comment-author">
                                                <input 
                                                    className={this.state.edit_baseinfo===0?"input-control":"input-control edit"} 
                                                    readOnly={this.state.edit_baseinfo===0?"readonly":""}
                                                    type="text"
                                                    defaultValue={this.state.userinfo.nickname}
                                                    maxLength = "10"
                                                    ref={input => this.nickname = input}
                                                    onChange={this.changeNickName} />
                                            </span>
                                        </div>
                                        <div className="comment-content"> 
                                            <span className="comment-author-at"></span>
                                            <p>
                                                <input
                                                    className={this.state.edit_baseinfo===0?"input-control":"input-control edit"} 
                                                    readOnly={this.state.edit_baseinfo===0?"readonly":""} type="text" 
                                                    defaultValue={this.state.userinfo.profile} 
                                                    ref={input => this.profile = input}
                                                    maxLength = "48"
                                                    onChange={this.changeProfile} />
                                            </p>
                                            <span className={this.state.edit_baseinfo===0?"comment-reply":"comment-reply hide"} style={{float:"right"}} onClick={this.editBaseInfo}>编辑</span>
                                            <span className={this.state.edit_baseinfo===1?"comment-reply":"comment-reply hide"} style={{float:"right"}} onClick={this.saveBaseInfo}>保存</span>
                                            <span className="err-tips">{this.state.errTips}</span>
                                        </div>
                                    </div>
                                    <div style={{display:this.state.edit_baseinfo===0?"none":"inline-flex",width:'100%',overflowX:"scroll"}}  className="comment-content">
                                        {
                                            global.constants.avatar_list.map(v=>{
                                                return (
                                                    <img key={v} className="avatar" onClick={(e)=>{this.setAvatar(v,e)}} src={global.getStaticHost()+v} width="80" height="80" alt="头像" ></img>
                                                )
                                            })
                                        }
                                    </div>
                                </div>
                            </li>
                        </ol>
                    </div>
                    <div className="clearfix" style={{textAlign:"center"}}>
                        <div className="comment-form">
                            <span className="comment-reply" ><Link to="/user/coin/list/2">兑换记录</Link></span>
                            {/* <span className="comment-reply"style={{float:"left"}}><Link to="/coin/buy">我发布的信息</Link></span> */}
                            {/* <span className="comment-reply" style={{float:"right"}}><Link to="/coin/records">发布信息</Link></span> */}
                        </div>
                    </div>
                    <div className="post-content">
                        <h3>我的金币</h3>
                    </div>
                    <div className="clearfix" style={{}}>
                        <div className="comment-form">
                            <h1 className="post-title" itemProp="name headline"> {this.state.userinfo.coin+".00"} </h1>
                            <span className="comment-reply" style={{}}><Link to="/user/coin/list/0">金币记录</Link></span>
                        </div>

                        <div className="comment-form" style={{minHeight:'470px'}}>
                            <h4 className="comment-reply">如何充值</h4>
                            <p>1.淘宝扫码购买金币</p>
                            <p><img style={{maxWidth:"200px"}} alt="商品二维码" src={global.getStaticHost()+"/images/taobao_goods.png"} /></p>
                            <p>2.输入商品订单号，完成金币充值</p>
                            <input type="text"  max-length="40" id="order_id" className="form-control input-control clearfix" placeholder="" style={{maxWidth:"100%"}} ref={input => this.order_id = input} />
                            <span className="err-tips">{this.state.errCoinMsg}</span>
                            <button type="submit" className="submit" id="login-btn" onClick={this.addCoin}>提交</button> 
                            <p></p>
                        </div>
                    </div>
                    {/* <div className="">
                        <div className="post-content">
                            <h3>审核</h3>
                        </div>
                    </div>
                    <div className="clearfix" style={{}}>
                        <div className="comment-form">
                            
                        </div>
                    </div> */}
                </div>
            )
        }
    }
}