import React from 'react';
import Header from '../component/header';
import Footer from  '../component/footer';
import UserComponent from  '../component/user';
import { Redirect } from 'react-router-dom'

class User extends React.Component {
  constructor(props){
    super(props)
    this.state={
      login_info:{is_login:0,token:""}
    }
  }

  componentWillMount() {
    this.setState({login_info:global.is_login()})
  }
  render(){
    // console.log(this.state.login_info)
    if (this.state.login_info.is_login === 0) {
        return (<Redirect to="/user/login" />);
    } else {
      return (
        <div id="container">
          <Header />
          <UserComponent history={this.props.history} is_login={this.state.login_info.is_login} token={this.state.login_info.token} />
          <Footer />
        </div>
      );
    }
  }
}
export default User;