import React from 'react';
import Header from '../component/header';
import Footer from  '../component/footer';
import ExchangeComponent from  '../component/exchange';
import { Redirect } from 'react-router-dom'

export default class Exchange extends React.Component {
  constructor(props){
    super(props)
    this.state={
      login_info:{"is_login":0,"token":""},
      vid:""
    }
  }

  componentWillMount() {
      this.setState({login_info:global.is_login(),vid:this.props.match.params.vid})
  }

  render(){
    if (this.state.login_info.is_login === 0) {
      return (<Redirect to="/user/login" />);
    } else {
      return (
        <div id="container">
          <Header />
          <ExchangeComponent history={this.props.history} is_login={this.state.login_info.is_login} token={this.state.login_info.token} vid={this.state.vid} />
          <Footer />
        </div>
      );
    }
  }
}