import React from 'react'
import Request from '../utils/request'
import exchangeCss from '../scss/exchange.module.scss'
import {Link} from 'react-router-dom'

export default class Comment extends React.Component {
    constructor(props){
        super(props)
        this.state={
            errTips:"",
            errMsg:"",
            version_info:{},
            result_data:[],
        }
        this.exchange = this.exchange.bind(this)
        this.getVersionInfo = this.getVersionInfo.bind(this)
        this.request = 0
    }

    componentDidUpdate(){
        global.setTitle("兑换 - "+global.constants.base_title)
    }

    exchange(){
        if (this.request === 0 ){
            this.request = 1
            Request({
                url:'/user/coinexchange',
                method:'post',
                headers:{"X-Token":this.props.token},
                data:{"vid":this.props.vid}
              }).then((res)=>{
                    if (res.code === 200) {
                        console.log(res.data)
                        this.setState({result_data:res.data.list,errTips:""})
                    } else {
                        this.setState({errTips:res.msg})
                    }
                    this.request = 0
              })
        }
    }

    getVersionInfo(){
        console.log(this.props.vid)
        Request({
            url:'/versioninfo',
            method:'post',
            headers:{"X-Token":this.props.token},
            data:{"vid":this.props.vid}
          }).then((res)=>{
              if (res.code === 200) {
                  console.log(res.data)
                  this.setState({version_info:res.data,errMsg:""})
              } else {
                this.setState({errMsg:res.msg})
              }
          })
    }

    componentWillMount(){
        this.getVersionInfo()
    }
    render(){
        console.log("render",this.state.errMsg )
        if (this.state.errMsg !== "") {
            return (
                <div id="exchange-page" className="main-content page-page" style={{paddingTop:"50px"}}>
                    <div className="post-content">
                        <h3>出错了</h3>
                    </div>
                    <div id="comment-2806">
                        <div className="comment-form">
                            <span className={exchangeCss["errmsg-tips"]}>{this.state.errMsg}</span>
                            
                        </div>
                        <div className="comment-content">
                            <Link to="/"> {"<<浏览其他商品"}</Link>
                        </div>
                    </div>
                </div>
            )
        } else {
            console.log(this.state.result_data.length)
            if (this.state.result_data.length=== 0) {
                return (
                    <div id="exchange-page" className="main-content page-page" style={{paddingTop:"50px"}}>
                        <div className="post-content">
                            <h3>兑换</h3>
                        </div>
                        <div id="comment-2806">
                            <div className="comment-form"> 
                                <div className="comment-content"> 
                                    <span className="comment-author-at"></span>
                                    <p className={exchangeCss["exchange-title"]}>{this.state.version_info.oname}</p>
                                    <p className={exchangeCss["exchange-subtitle"]}>{this.state.version_info.desc}</p>
                                    <p></p>
                                    
                                </div>
                                <div className="comment-content" style={{textAlign:"right",marginBottom:"50px"}}> 
                                    <span className={exchangeCss["exchange-coin"]}>-{this.state.version_info.sold_coin}</span>
                                    <span className={exchangeCss["exchange-coinname"]}> 金币</span>
                                    {/* <div className="exchange-coinname">我的金币:200</div> */}
                                    <div className={exchangeCss["err-tips"]}>{this.state.errTips}</div>
                                </div>
                                <div className="comment-content">
                                    
                                    <button type="submit" className="submit" style={{}} onClick={this.exchange}>确定</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    )
                } else {
                    return (
                        <div id="exchange-page" className="main-content page-page" style={{paddingTop:"50px"}}>
                            <div className="post-content">
                                <h3>兑换成功</h3>
                            </div>
                            {
                                this.state.result_data.map((v)=>{
                                    // console.log(v)
                                    // var share_content = v.share_content
                                    // var link = share_content.match(/(https:\/\/pan.baidu.com[a-z0-9A-Z_/]+)/)[0]
                                    // var code = share_content.match(/(:[0-9A-Za-z]{4})/g)[1].slice(1,5)
                                    // console.log(link,code)
                                    return (
                                        <div key={v.vid} >
                                            <div className="comment-form"> 
                                                <div className="comment-content"> 
                                                    <span className="comment-author-at"></span>
                                                    <p className={exchangeCss["exchange-title"]}>{this.state.version_info.oname}</p>
                                                    <p className={exchangeCss["exchange-subtitle"]}>{this.state.version_info.desc}</p>
                                                    <p>链接：<span className="share_link"><a href={v.share_link} target="blank">{""+v.share_link}</a></span></p>
                                                    <p>提取码：<span className="share_secret">{v.share_secret}</span></p>
                                                </div>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                            <div className="comment-content">
                                <Link to="/"> {"<<浏览其他商品"}</Link>
                            </div>
                        </div>
                    )
                }
        }
    }
}