import React from 'react'
import PageView from './pageview'
import {Link} from 'react-router-dom'
import Request from '../utils/request'

class Main extends React.Component {
    constructor(props){
        super(props)
        this.state={
            data:[],
            oid:"",
            page_size:12,
            category:0,
            has_next:3
        }
        this.getNextPage = this.getNextPage.bind(this)
    }
    getNextPage(){
        this.setState({has_next:3})
        Request({
            url:'/list',
            method:'post',
            header:{},
            data:{"page_size":this.state.page_size,"category":this.state.category,"oid":this.state.oid}
          }).then((res)=>{
              if (res.code === 200) {
                  var has_next = 1
                  if (res.data.list.length < this.state.page_size) {
                    has_next = 0
                  }
                  var oid = this.state.oid
                  if (res.data.list.length>0) {
                    oid = res.data.list[res.data.list.length-1].oid
                  }

                  var data = this.state.data

                  res.data.list.map(v=>{
                      data.push(v)
                      return v
                  })

                  this.setState({data:data,has_next:has_next,oid:oid})
              }
          })
    }
    componentWillMount(){
        this.getNextPage()
    }
    render(){
        return (
            <div className="main-content index-page clearfix ">
                <div className="post-lists">
                    <div className="post-lists-body">
                        {
                            this.state.data.map((v,k)=>{
                                var item_url = "/item/"+v.oid
                                var show_name = v.name
                                if (v.play_year!==0){
                                    show_name += "（"+v.play_year+"）"
                                }
                                var mainphoto = JSON.parse(v.images)[0]
                                return (
                                    <div className="post-list-item" key={v.oid}>
                                        <div className="post-list-item-container">
                                            <div className="item-thumb bg-deepgrey"                                           style= {{
                                                    backgroundImage: `url("${mainphoto}")`
                                                }}>
                                            </div>
                                            <Link  to={item_url}>
                                                <div className="item-desc">
                                                    <p>
                                                        {v.desc}
                                                    </p>
                                                </div>
                                            </Link>
                                            <div className="item-slant reverse-slant bg-deepgrey">
                                            </div>
                                            <div className="item-slant">
                                            </div>
                                            <div className="item-label">
                                                <div className="item-title">
                                                    <Link to={item_url}>{show_name}</Link>
                                                </div>
                                                <div className="item-meta clearfix">
                                                    {/* <div className="item-meta-ico bg-ico-note"
                                                        style={{
                                                            background: 'url(/images/bg-ico.png) no-repeat',
                                                            backgroundSize:'40px auto'
                                                        }}>
                                                    </div> */}
                                                    <div className="item-meta-cat">
                                                        {
                                                            JSON.parse(v.style).map((tag,k)=>{
                                                                if (k<3){
                                                                return (<Link key={tag.short_str} to={tag.short_str}>#&nbsp;{tag.name}</Link>)
                                                                }else{
                                                                    return ""
                                                                }
                                                            })
                                                        }
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
                <PageView has_next={this.state.has_next} getPage={this.getNextPage} />
            </div>
        )
    }
}

export default Main