import React from 'react'

import ChinaMap from './ncovchinamap';
import HubeiMap from './ncovhubeimap';
import StatMap from './ncovstat';
// import HubeiStatMap from './hubeistat';
import CityTable from './ncovtable';
import Timeline from './ncovtimeline';
// import Echarts from 'echarts'
// import chinaJson from 'echarts/map/json/china.json';
// import Dropdown from 'react-dropdown'

import Request from '../utils/request'

class ncov extends React.Component {
    constructor(props){
        super(props)
        this.state={
            maininfo :"[]",
            province :"[]",
            country  :"[]",
            province_city:"[]",
            stat_data:{},
            map_max_count:10000,
            selected:0,
        }
        // console.log(this.props.cate_name)
        this.getData = this.getData.bind(this)
    }
    componentDidMount(){
        global.setTitle("实时更新-关注武汉新型冠状病毒肺炎")
        
    }
    
    getData(){
        var path = "/ncov/getdata"
        var post_data = {}
        
        Request({
            url:path,
            method:'post',
            header:{},
            data:post_data
          }).then((res)=>{
              if (res.code === 200) {
                  this.setState(res.data)
              }else {
                  console.log("获取数据失败")
              }
          })
    }
    componentWillMount(){
        this.getData()
    }
    
    render () {
        var maininfo = JSON.parse(this.state.maininfo)
        
        return (
            <article className="main-content bg-white" style={{}}>
                <div id="post-content" className="post-content">
                    <figure className="wp-block-image" style={{marginTop:"40px"}}>
                        <img src={"/images/nocvhead.jpg"} alt="Hello World" className="wp-image-23 ncov-box" srcSet={"/images/nocvhead.jpg"}></img>
                    </figure>
                    <div className="no-selected ncov-box" style={{fontSize:".8rem",height:"1rem",paddingLeft:"10px"}}>截至{global.getLocalTime2(maininfo.modifyTime/1000)}疫情数据：</div>
                    <p></p>
                    <div style={{fontSize:"1.2em",textAlign:"center"}} className="no-selected ">
                        <div className="ncov-box">
                            <div className="ncovCountName">确诊</div>
                            <div className="ncovCountName">疑似</div>
                            <div className="ncovCountName">治愈</div>
                            <div className="ncovCountName">死亡</div>
                        </div>
                        <div className="ncov-box">
                            <span className="ncovCountNum quezhen" style={{fontWeight:"bold"}}> {maininfo.confirmedCount} </span>
                            <span className="ncovCountNum quezhen" style={{fontWeight:"bold"}}> {maininfo.suspectedCount} </span>
                            <span className="ncovCountNum zhiyu" style={{fontWeight:"bold"}}> {maininfo.curedCount} </span>
                            <span className="ncovCountNum siwang" style={{fontWeight:"bold"}}> {maininfo.deadCount} </span>
                        </div>
                    </div>
                    
                    <p></p>
                    {/* 疫情地图 */}
                    <ChinaMap province={this.state.province} map_max_count={this.state.map_max_count}/>
                    <HubeiMap province_city={this.state.province_city} map_max_count={this.state.map_max_count} />
                    {/* <p></p> */}
                    {/* 疫情趋势 */}
                    <StatMap stat_data={this.state.stat_data} maininfo={maininfo}/>
                    {/* <p></p> */}
                    {/* <HubeiStatMap stat_data={this.state.stat_data}/> */}
                    {/* 城市统计 */}
                    <CityTable province_city={this.state.province_city} country={this.state.country}/>
                    <p></p>
                    {/* 实时动态 */}
                    <Timeline />
                </div>
                <footer id="footer" className="footer">
                {/* <div style={{textAlign:"center"}}># 武汉加油 #</div> */}
                </footer>
            </article>
        )
    }
}

export default ncov