import React from 'react'

class share extends React.Component {
    constructor(props){
        super(props)
        this.state={
            isshare:0
        }
        this.show = this.show.bind(this)
        this.hide = this.hide.bind(this)
    }
    show(){
        this.setState({isshare:1})
    }
    hide(){
        this.setState({isshare:0})
    }

    share
    render(){
        return(
            <div className="ncov-bx">
            <div className="ncov-bx share-btn" onClick={this.show}></div>
            <div className={this.state.isshare===1?"share-bg":"share-bg hide"} onClick={this.hide}>
                <span className="sharebg-img"></span>
                <span className="sharebg-text">点击右上角 "更多"，<p></p>分享给好友或朋友圈</span>
            </div>
            </div>
        )
    }
}

 export default share 