import React from 'react'
import Headroom from 'react-headroom'
import { Link } from 'react-router-dom'
import Request from '../utils/request'


class Header extends React.Component {
    constructor(props){
        super(props)
        this.state={
          test:{},
          data:[],
          is_login:0,
          token:"",
          pintype:"",
        }
      }
    componentDidMount(){
        if ( window._.getStore("token")!== null && window._.getStore("token")!== "" ){
            this.setState({is_login:1,token:window._.getStore("token")})
        }
        Request({
          url:'/cats',
          method:'post',
          data:{"Header":{},"Body":{}}
        }).then((res)=>{
            if (res.code === 200) {
                this.setState({data:res.data})
            }
        })
    }
    render(){
        return (
            <Headroom className={`header bg-white animated ${this.state.pintype}`} style={{
                WebkitTransition: 'all .5s ease-in-out',
                MozTransition: 'all .5s ease-in-out',
                OTransition: 'all .5s ease-in-out',
                transition: 'all .5s ease-in-out',
              }}
              onPin={() => this.setState({pintype:"slideDown"})}
              onUnpin={() => this.setState({pintype:"slideUp"})}
              >
                    <div className="navbar-container">
                        <Link to="/" className="navbar-logo">{global.constants.name}</Link>
                        <div className="navbar-menu">
                            {
                                this.state.data.map((v,k) => {
                                    return  (<span key={v.short_str}><Link to={v.short_str}>{v.name}</Link></span>)
                                })
                            }
                        </div>
                        {
                            this.state.is_login ? <UCenterLink token={this.state.token} />:<LoginLink />
                        }
                        
                        <Link to="/search" className="navbar-search"><span className="icon-search"></span></Link>
                        
                        <div className="navbar-mobile-menu">
                            <span className="icon-menu cross"><span className="middle"></span></span>
                            <ul>
                            {
                                this.state.data.map((v,k) => {
                                    return  (<li key={v.short_str}><Link to={v.short_str}>{v.name}</Link></li>)
                                })
                            }
                            </ul>
                        </div>
                    </div>
            </Headroom>
        )
    }
}

class UCenterLink extends React.Component {
    // constructor(props){
    //     super(props)
    //   }
    render(){
        return (
            <span className="post-tags" style={{border:"none"}}><Link to={'/user/center'} className="navbar-user">用户中心</Link></span>
        )
    }
}

class LoginLink extends React.Component {
    // constructor(props){
    //     super(props)
    //   }
    render(){
        return (
            <span className="post-tags" style={{border:"none"}}><Link to="/user/login" className="navbar-user">登录</Link></span>
        )
    }
}

export default Header