import React from 'react'
import ImageGallery from 'react-image-gallery';
import ReactPlayer from 'react-player'
import "react-image-gallery/styles/css/image-gallery.css";
export default class gallery extends React.Component{
    
    constructor(props) {
        super(props);
        this.state = {
        showIndex: false,
        // showBullets: true,
        infinite: true,
        showThumbnails: false,
        showFullscreenButton: true,
        showGalleryFullscreenButton: true,
        showPlayButton: false,
        showGalleryPlayButton: true,
        showNav: true,
        isRTL: false,
        slideDuration: 450,
        slideInterval: 2000,
        slideOnThumbnailOver: false,
        thumbnailPosition: 'bottom',
        showVideo: {},
        staticHost:global.getStaticHost()+"/images/gallery/"
        };
        this.staticHost = global.getStaticHost()
        console.log(this.props.showFullscreenButton)
        
        // this.images = [
        // {
        //     thumbnail: this.state.staticHost+`4v.jpg`,
        //     original: this.state.staticHost+`4v.jpg`,
        //     // embedUrl: 'https://www.youtube.com/embed/4pSzhZ76GdM?autoplay=1&showinfo=0',
        //     // embedUrl: 'https://gss3.baidu.com/6LZ0ej3k1Qd3ote6lo7D0j9wehsv/tieba-smallvideo-transcode/2303489_33a4cb6139be32a9fc8cb8e5267312b3_0.mp4',
        //     embedUrl:this.state.staticHost+`/big_buck_bunny.mp4`,
        //     description: 'Render custom slides within the gallery',
        //     renderItem: this._renderVideo.bind(this)
        // },
        // {
        //     original: this.state.staticHost+`image_set_default.jpg`,
        //     thumbnail: this.state.staticHost+`image_set_thumb.jpg`,
        //     imageSet: [
        //     {
        //         srcSet: this.state.staticHost+`image_set_cropped.jpg`,
        //         media : '(max-width: 1280px)',
        //     },
        //     {
        //         srcSet: this.state.staticHost+`image_set_default.jpg`,
        //         media : '(min-width: 1280px)',
        //     }
        //     ]
        // },
        // {
        //     original: this.state.staticHost+`1.jpg`,
        //     thumbnail: this.state.staticHost+`1t.jpg`,
        //     originalClass: 'featured-slide',
        //     thumbnailClass: 'featured-thumb',
        //     description: 'Custom class for slides & thumbnails'
        // },
        // ].concat(this._getStaticImages());

        this.images = []
        this.props.images.map(v=> {
            if (typeof(v) == "string") {
                this.images.push({original:v})
            } else if (typeof(v) == "object" ){
                if (typeof(v.embedUrl) == "string" && v.embedUrl !== "") {
                    v.renderItem = this._renderVideo.bind(this)
                }
                this.images.push(v)
            }
            return v
        })
        console.log(this.images)
    }
    componentWillMount(){
        if (!this.props.showFullscreenButton) {
            this.setState({showFullscreenButton:false})
        }
    }
    componentDidUpdate(prevProps, prevState) {
        if (this.state.slideInterval !== prevState.slideInterval ||
            this.state.slideDuration !== prevState.slideDuration) {
        // refresh setInterval
        this._imageGallery.pause();
        this._imageGallery.play();
        }
    }

    _onImageClick(event) {
        console.debug('clicked on image', event.target, 'at index', this._imageGallery.getCurrentIndex());
    }

    _onImageLoad(event) {
        console.debug('loaded image', event.target.src);
    }

    _onSlide(index) {
        this._resetVideo();
        console.debug('slid to index', index);
    }

    _onPause(index) {
        console.debug('paused on index', index);
    }

    _onScreenChange(fullScreenElement) {
        console.debug('isFullScreen?', !!fullScreenElement);
    }

    _onPlay(index) {
        console.debug('playing from index', index);
    }

    _handleInputChange(state, event) {
        this.setState({[state]: event.target.value});
    }

    _handleCheckboxChange(state, event) {
        this.setState({[state]: event.target.checked});
    }

    _handleThumbnailPositionChange(event) {
        this.setState({thumbnailPosition: event.target.value});
    }

    _getStaticImages() {
        let images = [];
        for (let i = 2; i < 12; i++) {
        images.push({
            original: this.state.staticHost+`${i}.jpg`,
            thumbnail:this.state.staticHost+`${i}t.jpg`
        });
        }

        return images;
    }

    _resetVideo() {
        this.setState({showVideo: {}});

        if (this.state.showPlayButton) {
        this.setState({showGalleryPlayButton: true});
        }

        if (this.state.showFullscreenButton) {
        this.setState({showGalleryFullscreenButton: true});
        }
    }

    _toggleShowVideo(url) {
        var showVideo = this.state.showVideo
        showVideo[url] = !Boolean(this.state.showVideo[url]);
        this.setState({
        showVideo: showVideo
        });

        if (this.state.showVideo[url]) {
        if (this.state.showPlayButton) {
            this.setState({showGalleryPlayButton: false});
        }

        if (this.state.showFullscreenButton) {
            this.setState({showGalleryFullscreenButton: false});
        }
        }
    }

    _renderVideo(item) {
        return (
        <div className='image-gallery-image'>
            {
            this.state.showVideo[item.embedUrl] ?
                <div className='video-wrapper'>
                    <span
                    className='close-video'
                    onClick={this._toggleShowVideo.bind(this, item.embedUrl)}>
                    </span>
                    <ReactPlayer className="video_contain" width='100%' height='100%' url={item.embedUrl} playing controls />
                </div>
            :
                <span onClick={this._toggleShowVideo.bind(this, item.embedUrl)}>
                <div className='play-button'></div>
                <img src={item.original} alt="img"/>
                {
                    item.description &&
                    <span
                        className='image-gallery-description'
                        style={{right: '0', left: 'initial'}}
                    >
                        {item.description}
                    </span>
                }
                </span>
            }
        </div>
        );
    }

    render() {
        if (this.images.length === 0) {
            return (<span></span>)
        }else {
            return (
                <section className='app' style={{}}>
                    <ImageGallery
                    ref={i => this._imageGallery = i}
                    items={this.images}
                    lazyLoad={true}
                    onClick={this._onImageClick.bind(this)}
                    onImageLoad={this._onImageLoad}
                    onSlide={this._onSlide.bind(this)}
                    onPause={this._onPause.bind(this)}
                    onScreenChange={this._onScreenChange.bind(this)}
                    onPlay={this._onPlay.bind(this)}
                    infinite={this.state.infinite}
                    showBullets={this.state.showBullets}
                    showFullscreenButton={this.state.showFullscreenButton && this.state.showGalleryFullscreenButton}
                    showPlayButton={this.state.showPlayButton && this.state.showGalleryPlayButton}
                    showThumbnails={this.state.showThumbnails}
                    showIndex={this.state.showIndex}
                    showNav={this.state.showNav}
                    isRTL={this.state.isRTL}
                    thumbnailPosition={this.state.thumbnailPosition}
                    slideDuration={parseInt(this.state.slideDuration)}
                    slideInterval={parseInt(this.state.slideInterval)}
                    slideOnThumbnailOver={this.state.slideOnThumbnailOver}
                    additionalClass="app-image-gallery"
                    />
                </section>
            );
        }
    }
}