global.setTitle = function(title){
    document.title = title
}

global.getStaticHost = function(){
    var host = 'https://xiaxm.com/static'
    if (process.env.NODE_ENV === "development") {
        host  = "http://"+document.location.hostname+":3000/static.xiaxm.com"
    }
    return host
} 

global.getLocalTime= function(timestamp) {
    if (timestamp) {
        var time = new Date(timestamp*1000);
        var y = time.getFullYear(); //getFullYear方法以四位数字返回年份
        var M = time.getMonth() + 1; // getMonth方法从 Date 对象返回月份 (0 ~ 11)，返回结果需要手动加一
        var d = time.getDate(); // getDate方法从 Date 对象返回一个月中的某一天 (1 ~ 31)
        var h = time.getHours(); // getHours方法返回 Date 对象的小时 (0 ~ 23)
        var m = time.getMinutes(); // getMinutes方法返回 Date 对象的分钟 (0 ~ 59)
        var s = time.getSeconds(); // getSeconds方法返回 Date 对象的秒数 (0 ~ 59)
        return y + '-' + (M < 10 ? "0" + M : M) + "-" + (d < 10 ? "0" + d : d) + ' ' + (h < 10 ? "0" + h : h) + ":" + (m < 10 ? "0" + m : m)  + ':' + (s < 10 ? "0" + s : s);
      } else {
          return '';
      }
}

global.getLocalTime2= function(timestamp) {
    if (timestamp) {
        var time = new Date(timestamp*1000);
        var y = time.getFullYear(); //getFullYear方法以四位数字返回年份
        var M = time.getMonth() + 1; // getMonth方法从 Date 对象返回月份 (0 ~ 11)，返回结果需要手动加一
        var d = time.getDate(); // getDate方法从 Date 对象返回一个月中的某一天 (1 ~ 31)
        var h = time.getHours(); // getHours方法返回 Date 对象的小时 (0 ~ 23)
        var m = time.getMinutes(); // getMinutes方法返回 Date 对象的分钟 (0 ~ 59)
        // var s = time.getSeconds(); // getSeconds方法返回 Date 对象的秒数 (0 ~ 59)
        return y + '-' + (M < 10 ? "0" + M : M) + "-" + (d < 10 ? "0" + d : d) + ' ' + (h < 10 ? "0" + h : h) + ":" + (m < 10 ? "0" + m : m) ;
      } else {
          return '';
      }
}

global.is_login = function(){
    var token = window._.getStore("token")
    if (token!== null && token!=="" ){
        return {is_login:1,token:token}
    }
    return {is_login:0,token:""}
}
