import React from 'react'
import PageView from './pageview'
import {Link} from 'react-router-dom'
import Request from '../utils/request'

class Cate extends React.Component {
    constructor(props){
        super(props)
        this.state={
            data:[],
            oid:"",
            page_size:12,
            cate_name:"",
            has_next:1,
            cate_show_name:"",
            cate_link:""
        }
        // console.log(this.props.cate_name)
        this.getNextPage = this.getNextPage.bind(this)
    }
    getNextPage(){
        this.setState({has_next:3})
        var path = "/catelist"
        var post_data = {"page_size":this.state.page_size,"oid":this.state.oid}
        if (this.props.cate_name !== undefined) {
            post_data.cate_name = this.props.cate_name
        } else if (this.props.search_str !== undefined){
            path = "/searchlist"
            post_data.search_str = this.props.search_str
        }
        Request({
            url:path,
            method:'post',
            header:{},
            data:post_data
          }).then((res)=>{
              if (res.code === 200) {
                  var has_next = 1
                  if (res.data.list.length < this.state.page_size) {
                    has_next = 0
                  }
                  var oid = this.state.oid
                  if (res.data.list.length>0) {
                    oid = res.data.list[res.data.list.length-1].oid
                  }

                  var data = this.state.data

                  res.data.list.map(v=>{
                      data.push(v)
                      return v
                  })
                 
                  if (res.data.list.length === 0) {
                      has_next =2 //该页没有数据
                  }
                  this.setState({data:data,has_next:has_next,oid:oid,cate_show_name:res.data.cate_show_name,cate_link:res.data.cate_link})
              } else {
                this.setState({data:[],has_next:2,oid:oid,cate_show_name:this.props.cate_name,cate_link:""})
              }
          })
    }
    componentWillMount(){
        this.getNextPage()
    }
    render(){
        global.setTitle(this.state.cate_show_name+" - "+global.constants.base_title)
        return (
            <div className="main-content common-page clearfix">
                <div className="common-item">
                    <div className="common-title">
                       <Link to={this.state.cate_link}>{this.state.cate_show_name}</Link>
                    </div>
                    <div className="post-lists">
                    {
                            this.state.data.map((v,k)=>{
                                var item_url = "/item/"+v.oid
                                var show_name = v.name
                                if (v.play_year!==0){
                                    show_name += "（"+v.play_year+"）"
                                }
                                var mainphoto = JSON.parse(v.images)[0]
                                return (
                                    <div className="post-list-item" key={v.oid}>
                                        <div className="post-list-item-container">
                                            <div className="item-thumb bg-deepgrey"                                           style= {{
                                                    backgroundImage: `url("${mainphoto}")`
                                                }}>
                                            </div>
                                            <Link  to={item_url}>
                                                <div className="item-desc">
                                                    <p>
                                                        {v.desc}
                                                    </p>
                                                </div>
                                            </Link>
                                            <div className="item-slant reverse-slant bg-deepgrey">
                                            </div>
                                            <div className="item-slant">
                                            </div>
                                            <div className="item-label">
                                                <div className="item-title">
                                                    <Link to={item_url}>{show_name}</Link>
                                                </div>
                                                <div className="item-meta clearfix">
                                                    {/* <div className="item-meta-ico bg-ico-note"
                                                        style={{
                                                            background: 'url(/images/bg-ico.png) no-repeat',
                                                            backgroundSize:'40px auto'
                                                        }}>
                                                    </div> */}
                                                    <div className="item-meta-cat">
                                                        {
                                                            JSON.parse(v.style).map((tag,k)=>{
                                                                if (k<3){
                                                                return (<Link key={tag.short_str} to={tag.short_str}>#&nbsp;{tag.name}</Link>)
                                                                } else {
                                                                    return ""
                                                                }
                                                            })
                                                        }
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
                <PageView has_next={this.state.has_next} getPage={this.getNextPage} />
            </div>
        )
    }
}

export default Cate