import React from 'react'
// import { Link } from 'react-router-dom';
import Request from '../utils/request'

export default class Login extends React.Component {
    constructor(props){
        super(props)
        this.state={
            data:{},
            code:"",
            errMsg:"",
        }
        this.login = this.login.bind(this)
        this.change = this.change.bind(this)
    }
    //
    change(){
        this.setState({code:this.input.value})
    }
    //登录接口
    login(){
        var code = this.input.value
        this.setState({code:code})
        if (code.length !== 6) {
            this.setState({errMsg:"登录口令长度错误"})
            return 
        }
        Request({
            url:'/auth',
            method:'post',
            header:{"content-Type":"application/json"},
            data:{code:this.state.code}
          }).then((res)=>{
              if (res.code === 200) {
                window._.setStore("token",res.data.token)
                this.setState({errMsg:"login success."})
                this.props.history.push('/user/center')
                //第一次登录跳转到用户信息编辑页面，输入用户名，性别
              } else {
                  this.setState({errMsg:res.msg})
              }
          })
    }

    render(){
        global.setTitle("登录 | "+global.constants.base_title)
        return (
            <div id="login-page" className="main-content page-page" style={{paddingTop:"50px"}}>
                <div className="">
                    <div className="post-content">
                        <h3>使用微信口令登录</h3>
                    </div>
                </div>
                <div className="clearfix"> 
                    <div className="comment-form" style={{minHeight:'120px'}}>
                        <input type="text" name="auth_code" max-length="6" id="auth_code" className="form-control input-control clearfix" placeholder="000000" style={{maxWidth:"100%",letterSpacing:"5px"}} ref={input => this.input = input} onChange={this.change} value={this.state.code} />
                        <span className="err-tips">{this.state.errMsg}</span>
                        <button type="submit" className="submit" id="login-btn" onClick={this.login}>登录</button> 
                    </div>
                </div>

                <div className="clearfix" style={{marginBottom:"40px",fontSize:".8em"}}>
                    <ol className="comment-list">
                        <li id="li-comment-2806" className="comment-body comment-parent comment-odd">
                            <div id="comment-2806">
                                <div className="comment-view">
                                    <h4>如何获取六位微信口令</h4>
                                    <div className="comment-content"> 
                                        <blockquote>
                                            <ul>
                                                <li><p>微信扫描下方二维码</p></li>
                                                <li><p>关注公众号</p></li>
                                                <li><p>回复login</p></li>
                                            </ul
                                        ></blockquote>
                                    </div>
                                </div>
                            </div>
                        </li>
                    </ol>
                </div>
                
                <div className="clearfix" style={{textAlign:"center"}}>
                    <div className="comment-form">
                        <img style={{maxWidth:"200px"}} alt="wechat mp qrcode" src={global.getStaticHost()+"/images/qrcode.jpg"} />
                    </div>
                </div>
            </div>
        )
    }
}