import React from 'react'
import Request from '../utils/request'

class ncovtimeline extends React.Component{
    constructor(props){
        super(props)
        this.state={list:[],next:0}
        this.getReport = this.getReport.bind(this)
        this.list = []
    }

    getReport(){
        if (this.list.length === 0) {
            var path = "/ncov/getreport"
            var post_data = {}
            
            Request({
                url:path,
                method:'post',
                header:{},
                data:post_data
            }).then((res)=>{
                if (res.code === 200) {
                    this.list = res.data.list
                    var tmpList = []
                    this.list.map((v,k)=>{
                        if (k<30){
                            return tmpList.push(v)
                        }
                        return ""
                    })
                    this.setState({list:tmpList})
                    // console.log(this.state)
                } else {
                    console.log("获取数据失败")
                }
            })
        } else {
            if (this.state.list.length < this.list.length){
                this.setState({list:this.list,next:1})
            }
        }
    }

    componentWillMount(){
        this.getReport()
    }
    render(){
        return(
            <div id="ncov-timeline"  className="no-selected">
                <div className="ncov-title">实时动态</div>
                <div className="timeline">
                    {
                        this.state.list.map((v,k)=>{
                            return (
                                <div key={v.id} className="timeline_events">
                                    <span></span>
                                    <div className="timeline_date">{global.getLocalTime(v.pubDate/1000)}</div>
                                    <div className="tiemline_text">
                                        <span className="tiemline_text_title">{v.title}</span>
                                        <span className="tiemline_text_detail">{v.summary}</span>
                                        <span className="timeline_date_from"><a target="blank" href={v.sourceUrl}>{v.infoSource}</a></span>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
                
                <div style={{textAlign:"center"}} onClick={this.getReport}><span className="timeline_date_from">{this.state.next === 0 ? "查看更多":"这是我的底线，一起为武汉加油吧！"}</span></div>
            </div>
        )
    }
}

export default ncovtimeline