import React from 'react'
import Headroom from 'react-headroom'
// import { Link } from 'react-router-dom'
// import Request from '../utils/request'


class Header extends React.Component {
    constructor(props){
        super(props)
        this.state={
            selected:0,
            clientHeight:0,
            scrollTop:0,
            scrollHeight:0,
            scrollY:0
        }
        this.scrollToAnchor = this.scrollToAnchor.bind(this)
    }

    scrollToAnchor = (anchorName,no) => {
        if (anchorName) {
            let anchorElement = document.getElementById(anchorName);
            if(anchorElement) { anchorElement.scrollIntoView(); }
        }
        this.setState({selected:no})
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
     }
     handleScroll=(event)=>{
            //滚动条高度
            let ctx=this;
            let clientHeight = document.documentElement.clientHeight; //可视区域高度
            let scrollTop  = document.documentElement.scrollTop;  //滚动条滚动高度
            let scrollHeight =document.documentElement.scrollHeight; //滚动内容高度
            let scrollY = window.scrollY
            var menu = 0
            var scrollNum = scrollY >0 ?scrollY : scrollTop
            if (scrollNum >=5290) {
                menu = 3
            } else if (scrollNum >=1952) {
                menu = 2
            // } else if (scrollNum >= 200) {
            //     selected = 1
            } else {
                menu = 0
            }
            ctx.setState({selected:menu,clientHeight:clientHeight,scrollTop:scrollTop,scrollHeight:scrollHeight,scrollY:scrollY})
    }
    render(){
        return (
            <Headroom className={`header bg-white animated ${this.state.pintype}`} style={{
                WebkitTransition: 'all .5s ease-in-out',
                MozTransition: 'all .5s ease-in-out',
                OTransition: 'all .5s ease-in-out',
                transition: 'all .5s ease-in-out',
              }}
              onPin={() => this.setState({pintype:"slideDown"})}
              onUnpin={() => this.setState({pintype:"slideUp"})}
              >
                    <div className="navbar-container" style={{height:"2.5rem",lineHeight:"2.5rem"}}>
                            {/* <span style={{fontSize:".5rem",position:"absolute",bottom:"0",lineHeight:".5rem"}}>{this.state.clientHeight},{this.state.scrollTop},{this.state.scrollHeight},{this.state.scrollY}</span> */}
                            <div onClick={()=>this.scrollToAnchor('post-content',0)} className={this.state.selected===0?"ncov-menu selected":"ncov-menu" }>疫情地图</div>
                            <div onClick={()=>this.scrollToAnchor('ncov-table',2)} className={this.state.selected===2?"ncov-menu selected":"ncov-menu" }>疫情数据</div>
                            <div onClick={()=>this.scrollToAnchor('ncov-timeline',3)} className={this.state.selected===3?"ncov-menu selected":"ncov-menu" }>实时动态</div>  
                    </div>
            </Headroom>
        )
    }
}

export default Header