import React from 'react';
import Header from '../component/header';
import Footer from  '../component/footer';
import { Link } from 'react-router-dom'

const Logo = (props) => {
    switch (props.type) {
        case "success":
            return (
                <div className="result-logo">
                    <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 48 48"><title>ic_done_48px</title>
                        <g className="nc-icon-wrapper" stroke="none" fill="#33a3f4">
                            <path d="M18 32.34L9.66 24l-2.83 2.83L18 38l24-24-2.83-2.83z"/>
                        </g>
                    </svg>
                </div>
            )
        case "fail":
            return (
                <div className="result-logo">
                    <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 48 48"><title>ic_close_48px</title>
                        <g className="nc-icon-wrapper" stroke="none" fill="#33a3f4">
                            <path d="M38 12.83L35.17 10 24 21.17 12.83 10 10 12.83 21.17 24 10 35.17 12.83 38 24 26.83 35.17 38 38 35.17 26.83 24z"/>
                        </g>
                    </svg>
                </div>
            )
        case "info":
            return (
                <div className="result-logo">
                    <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 48 48"><title>ic_info_outline_48px</title>
                        <g className="nc-icon-wrapper" stroke="none" fill="#33a3f4">
                            <path d="M22 34h4V22h-4v12zm2-30C12.95 4 4 12.95 4 24s8.95 20 20 20 20-8.95 20-20S35.05 4 24 4zm0 36c-8.82 0-16-7.18-16-16S15.18 8 24 8s16 7.18 16 16-7.18 16-16 16zm-2-22h4v-4h-4v4z"/>
                        </g>
                    </svg>
                </div>
            )
        case "wait":
            return (
                <div className="result-logo">
                    <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 48 48"><title>ic_schedule_48px</title>
                        <g className="nc-icon-wrapper" stroke="none" fill="#33a3f4">
                            <path d="M23.99 4C12.94 4 4 12.95 4 24s8.94 20 19.99 20C35.04 44 44 35.05 44 24S35.04 4 23.99 4zM24 40c-8.84 0-16-7.16-16-16S15.16 8 24 8s16 7.16 16 16-7.16 16-16 16zm1-26h-3v12l10.49 6.3L34 29.84l-9-5.34z"/>
                        </g>
                    </svg>
                </div>
            )
        default:
            return (<div></div>)
    }
}

export default class Result extends React.Component {
    constructor(props){
        super(props)
        this.state={
            title:"兑换成功",
            sub_title:"请前往我的订单查新购买的信息资源",
            from:"/user/exchange",
            to:"/user/bug",
            to_name:"查看",
            type:"wait", //info err success wait
        }
    }
    componentWillMount(){

    }


    render(){
        return (
            <div id="container">
            <Header />
            <div id="result-page">
                <article className="main-content page-page bg-white" style={{paddingTop:"50px",paddingBottom:"50px"}}>
                    <Logo type={this.state.type}/>
                    <div className="result-title">{this.state.title}</div>
                    <div className="result-subtitle">{this.state.sub_title}</div>
                    <div className="result-link">
                        <Link to={this.state.from}>{"<< 返回"}</Link>
                        <Link to={this.state.from} style={{float:"right"}}>{this.state.to_name+" >>"}</Link>
                    </div>
                </article>
            </div>
            <Footer />
            </div>
        );
    }
}