import React from 'react'
// import { Link } from 'react-router-dom';

class PageView extends React.Component {
    constructor(props){
        super(props)
        this.state={

        }
        this.getPage = this.getPage.bind(this)
    }
    getPage(){
        this.props.getPage()
    }

    render () {
        if (this.props.has_next === 1){
            return (
                <div className="lists-navigator clearfix">
                    <ol className="page-navigator">
                        <li className="current">
                            <span className="page-numbers" style={{cursor:"pointer"}} onClick={this.getPage}>下一页</span>
                        </li>
                    </ol>
                </div>
                )
        }else if (this.props.has_next === 2) {
            return (
                <div className="lists-navigator clearfix">
                    <ol className="page-navigator">
                        <li className="current">
                            <span className="page-numbers">抱歉，您想要的资源尚未收录。</span>
                        </li>
                    </ol>
                </div>
            )
        }else if (this.props.has_next === 3) {
            return (
                <div className="lists-navigator clearfix">
                    <ol className="page-navigator">
                        <li className="current">
                            <span className="page-numbers">正在加载数据...</span>
                        </li>
                    </ol>
                </div>
            )
        } else {
            return (
                <div className="lists-navigator clearfix">
                    <ol className="page-navigator">
                        <li className="current">
                            <span className="page-numbers">已经是最后一页了</span>
                        </li>
                    </ol>
                </div>
            )
        }
    }
}

export default PageView