import React from 'react';
import './App.css';
import IndexPage from './page/index';
import PrivacyPolicyPage from './page/privacy_policy';
import SearchPage from './page/search';
import CatePage from './page/cate';
import AboutPage from './page/about';
import NotFoundPage from './page/not_found';
import ItemPage from './page/item';
import TestPage from './page/test';
import LoginPage from './page/login';
import UserPage from './page/user';
import TimelinePage from './page/timeline';
import HelpPage from './page/help';
import ExchangePage from './page/exchange';
import ResultPage from './page/result';
import CoinPage from './page/coin';
import NcovPage from './page/ncov';

import './utils/localstorage'
import './config'
import './utils/common'
import { HashRouter, Route, Switch,Redirect } from 'react-router-dom'

const supportsHistory = !('pushState' in window.history)

function App() {
  return (
    <HashRouter forceRefresh={supportsHistory} keyLength={24} >
      <Switch> 
        <Route exact component={IndexPage} path='/' />
        <Route exact component={PrivacyPolicyPage} path='/privacy-policy' />
        <Route exact component={SearchPage} path='/search' />
        <Route exact component={NcovPage} path='/2019-nCoV' />
        <Route component={CatePage} path='/cate/:cate_name' />
        <Route component={CatePage} path='/tag/:cate_name' />
        <Route component={CatePage} path='/search/:search_str' />
        <Route component={CatePage} path='/collecion/:cate_name' />
        <Route component={CatePage} path='/celebrity/:cate_name' />
        <Route component={ItemPage} path='/item/:oid' />
        <Route component={AboutPage} path='/about' />
        <Route exact component={NotFoundPage} path='/404' />
        <Route component={TestPage} path='/test' />
        <Route component={CoinPage} path='/user/coin/list/:type' />

        {/* 登录 */}
        <Route exact component={LoginPage}path='/user/login' />
        {/* 用户中心 */}
        <Route component={UserPage}path='/user/center' />
        {/* 用户帮助 */}
        <Route exact component={HelpPage}path='/help' />
        {/* 产品日记 */}
        <Route exact component={TimelinePage} path='/timeline' />

        {/* 金币兑换 */}
        <Route component={ExchangePage} path='/user/exchange/:vid' />
        {/* 直接购买 流程更简单 */}
        {/* <Route component={ExchangePage} path='/user/buy/:vid' />   */}
        {/* 过渡提示(中间页)页 */}
        <Route component={ResultPage} path='/result' />
        <Redirect to='/' />

      </Switch>
    </HashRouter>
  );
}

export default App;
